import React, { useEffect, useState } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import * as _Util from '../store/Util'
import {
  UpdateDetails
} from '../actions/common'



import loadable from '@loadable/component'


const Icon2 = loadable(() => import('./Icons'))

const WithScroll = loadable(() => import('./scroll-decorator'))




const useObserveChanges = () => {
  const observeChanges =  useSelector(state => state.observeChanges); 
  const dispatch = useDispatch();
  
  return { 
    observeChanges
  }

}


const RowItemViewPort = (props) => {
   
    const {  _item, indX } = props;



    const [isVisible, setIsVisible] = useState(false); 
  
   
    let _state = _Util.getStore();
    let keys = _Util.getGlobalsKeys()
    _state["keys"] = keys;  
    let _filters = {}

   
  
    let ItId=`_${keys[28]}_${indX}_${keys[indX]}_`;
  
  
    const scrollhandlerView = (e) => { 
      let sc = e;
      let Elm = document.getElementById(ItId);    
      var dm = _Util.offset(Elm);
      var _top = dm.top;
      if(_top>=sc-50 && _top<=(sc+window.innerHeight)+350){      
        setIsVisible(true);
      }
    };
    let saIdname = ""
    let dayMls = 8640000;
    let showItem = _item.startTime >= ((new Date()).getTime()-dayMls);
    if(showItem){
      let usersFBList = _state["usersFBList"];
      const id  = _state["ActiveUser"];
      _filters = usersFBList && usersFBList[id] && usersFBList[id]['filters']  
      saIdname = _filters && _filters[_item.location] && _filters[_item.location]["name"]
    }
  
    return (
      <>
      {showItem?
        <div id={ItId}  className={'_w100  _dsplFlx spaceAround'}  >
          {isVisible?
          <>
            <div className={`pym81b boxCard`}>
              <p className={`_date_schedule_card `}>
                {`${_Util.date2pretyfy(_item.startTime)} ${_Util.time2pretyfy(_item.startTime)}`}
              </p>
              <p className={`_Id_card `}>
                {`${saIdname}`}
              </p>
              <div className={`_dsplFlx spaceAround `}>
                <div className={`_email_card `}>
                  {`${_item.hrs}HRS `}
                </div>
                <div className={`_email_card `}>
                  {`$${_item.pay}`}
                </div>
              </div>
            </div>
        </>
        :<WithScroll scrollhandler={(e)=>scrollhandlerView(e)}/>}
      </div>
    :null}
    </>
  
  
    )
  }
  
  
  


export default RowItemViewPort;


